import axios from 'axios';
const api =  async (row) => {
    const data = row.name + row.sender_name;
    const headers= {
        "Access-Control-Allow-Origin": "*"
    };
        
    console.log('data:::::', data);

    const response  = await axios.post('https://l6ue901rsk.execute-api.eu-west-1.amazonaws.com/real-time', {
            inputs: data
        }, headers);
        
    return response;
}

export default api;