import * as React from 'react';
import Box from '@mui/material/Box';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';

import api from '../services/api'

import UNSPSC from '../data/categories.json';

// console.log({UNSPSC});

export default function DropDown(props) {
    const [categories, setCategories] = React.useState([]);

    const getCategoryName= (label) => {
      console.log({label})
      const segment = label.substring(0,2) + '000000';
      console.log({segment})
      const segmentData = UNSPSC.filter((obj) => {
          console.log(typeof obj.id)
          return obj.id.toString() === segment
      });
      console.log({segmentData})

      const family = segmentData[0].children.filter((obj) => {
        return obj.id.toString() === label
      })
      console.log({family})

      return family[0].name
    }

    const getData = async () => {
        const result = await api(props.row);
        // console.log('result: ', result)
        setCategories([result.data[0]])
    }

    React.useEffect(() => {
        getData()
      }, []);
  const [selectedCategory, setSelectedCategory] = React.useState('');

  const handleChange = (event) => {
    setSelectedCategory(event.target.value);
  };

  return (
    <Box sx={{ minWidth: 120 }}>
      <FormControl fullWidth>
        <InputLabel id="demo-simple-select-label">Select Category</InputLabel>
        <Select
          labelId="demo-simple-select-label"
          id="demo-simple-select"
          value={selectedCategory}
          displayEmpty
          label="selectedCategory"
          onChange={handleChange}
        >
            {
                categories.map((category) => {
                    return (
                        <MenuItem 
                            value={category.label}
                            id={category.score}
                        >
                                {getCategoryName(category.label)}
                                 ( {
                                        Math.round(category.score * 100)
                                    }%)
                        </MenuItem>
                    )
                })
            }
          <MenuItem value={0}>No Matching Category</MenuItem>
          {/* <MenuItem value={10}>Ten</MenuItem>
          <MenuItem value={20}>Twenty</MenuItem>
          <MenuItem value={30}>Thirty</MenuItem> */}
        </Select>
      </FormControl>
    </Box>
  );
}